import React, { Component } from 'react'
import "./post.css"

export default class Post extends Component {
    
  render() {
    return (
          <div className='post'>
        <div className="avatar">
            <img className='avatar__pic' src={this.props.avatar} alt="" />
        </div>
        <div className="post__info">
            <div className="user__info">
                <h1 className="name">{this.props.name}</h1>
                <h2 className="username">{this.props.username}</h2>
            </div>
            <p className="description">{this.props.description}</p>
            {this.props.image && <img className='post__pic' src={this.props.image} alt="" />}
            <div className="post__options">
              <i className="fa-regular fa-comment comment"></i>
              <i className="fa-regular fa-heart like"></i>
              <i className="fa-solid fa-retweet retweet"></i>
            </div>
        </div>
        
      </div>
    )
  }
}

